@import "../../styles/item_attributes.scss";

.profile-page {
  z-index: 201 !important;
  .profile-menu-content {
    position: relative;
    --background: #{$darkBlue};
    --color: #{$white};

    .close-icon {
      position: absolute;
      right: 0;
      top: 0;
      padding: 20px;
      font-size: 2rem;
      font-weight: bold;
    }

    .menu-links {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      height: 95%;

      ul {
        padding: 0px;
        margin: 0px;
        margin-bottom: 30%;
        li {
          list-style: none;
          padding: 0px;
          margin: 0px;
          font-size: 1.5rem;
          display: flex;
          align-items: center;
          margin-left: 20px;
          padding-bottom: 20px;

          ion-icon {
            margin-right: 10px;
          }
        }
      }
    }

    .tnc {
      text-align: center;
      font-size: 1rem;
    }
  }
}
